@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.header-wrapper {
    text-align: right;
    line-height: 1.5;
    height: 5rem;
    width: 100%;
    height: auto;
    background: transparent;
    /* padding-top: 250px; */
    padding-bottom: 100px;
    background-image: url(/static/media/ALM_Logo.3608d58f.jpg);
    background-position: top left;
    background-size: 400px;
    background-attachment: local;
    background-repeat: no-repeat;
}

.nunito {
    font-size: 80px;
    font-weight: bold;
}

.raleway {
    font-size: 50px;
    font-weight: lighter;
    font-family: 'Raleway';
}

.header-content {
    height: auto;
    width: 80%;
    top: 15px;
    background: transparent;
    margin: 0 auto;
    font-size: xx-large;
}

.header-menu {
    text-align: left;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: transparent ;
}
.header-menu ul {
    list-style-type: none;
    margin: 0 auto;
}
.header-menu ul li {
    display: inline-block;
    width: 40rem;
}
.main-content-wrapper {
    color: #f6c90e;
    background-image: linear-gradient(to top, #303841 70%, #ffffff);
    padding-top: 300px;
}

.flex {
    margin: 0 auto;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.flex-column {
    margin: 0 auto;
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.flex-column-item {
    margin: 0px 10px 0px 10px;
    border: 5px outset #f6c90e;
    background: #ed6663;
    background-image: url("https://picsum.photos/200");
    background-position: center;
    background-size: cover;
    box-shadow: 0px 0px 8px 5px #ffffff;
    border-radius: 25%;
    width: 400px;
    height: auto;
    min-height: 300px;
    order: 1;
    perspective: 1000px;
    color: transparent;
}

.flex-column-item:hover {
    background-blend-mode: color-burn;
    color: #f6c90e 
}

.flex-item {
    width: 100%;
    color: #eeeeee;
    padding: 50px 30px 50px 0px;
}

.text-item-left {
    font-family: 'Raleway';
    line-height: 1.5;
    letter-spacing: 1.1;
    text-align: left;
    padding: 10px 0px 10px 0px;
}

.text-item-right {
    text-align: right;
    font-family: 'Raleway';
    line-height: 1.5;
    letter-spacing: 1.1;
    padding: 10px 0px 10px 0px;
}

.exp-image img {
    border: 10px solid black;
    max-width: 400;
    height: auto;
}
.project-wrapper {
    margin: 0 auto;
    height: auto;
    max-width: 750px;
}

img {
    max-width: 750px;
}
.iconbar-wrapper {
    color: black;
    left: 0rem;
    width: auto;
    height: auto;
}
.iconbar-list {
    background: radial-gradient(ellipse at bottom, #949494, transparent 45%);
}
.iconbar-list ul {
    /* padding-bottom: 10px; */
    margin: 0 auto;
    padding: 0px 0px 5px 0px;
    border-bottom: 3px solid black;
    border-right: 3px solid black;
    border-left: 3px solid black;
    transform: rotateY(15deg);
    display: inline-block;
    list-style-type: none;
}
.iconbar-list ul li {
    display: inline-block;
    list-style-type: none;
    width: 5rem;
}
.iconbar-list-flipped {
    background: radial-gradient(ellipse at top, #949494, transparent 45%);
}
.iconbar-list-flipped ul {
    margin: 0 auto;
    padding: 75px 0px 10px 0px;
    transform: rotateX(180deg);
    display: inline-block;
    list-style-type: none;
}
.iconbar-list-flipped ul li {
    opacity: .5;
    display: inline-block;
    list-style-type: none;
    width: 5rem;
}

.small-iconbar-list {
    display: none;
}

@media only screen and (max-width: 600px) {
    .small-iconbar-list {
        padding-left: 45%;
        display: flex;
    }
    .iconbar-list {
        display: none;
    }
    .iconbar-list-flipped {
        display: none;
    }
  }
.contact-form-wrapper {
    background: #303841;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    max-width: auto;
    margin: 0 auto;
    padding: 50px 20px 50px 20px;
    align-content: center;
    /* box-shadow: 5px 10px 25px 10px #000; */
}

.form-container {
    padding: 25px 30px 25px 30px;
    width: 400px;
    flex-basis: auto;
    order: 1;
    background-image: linear-gradient(to bottom, #303841, #eeeeee);
    border: 5px outset #7e8a97;
}

.target-container {
    width: 400px;
    flex-basis: auto;
    order: 1;
    height: auto;
    width: 100px;
}

.button {
    color: black;
    background-color: #7e8a97;
    margin: 0 auto;
    width: 10em;
}

.footer-wrapper {
    width: 100%;
    height: 100px;
    background-image: linear-gradient(to bottom, #303841, #ffffff);
}

.title {
    padding-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.footer-layer-one {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0 auto;
    width: 80%;
    height: 80px;
}

.footer-icon {
    padding: 100px 100px 100px 50px;
}
body {
    color: #303841;
}

.home {
    font-family: 'Nunito';
    display: flexbox;
    flex-direction: column;
    background-color: #ffffff;
    min-width: 100%;
    height: auto;
}
.main {
    height: auto;
    width: 100%;
}
